import Vue from 'vue';
import Router from 'vue-router';

import Homepage from 'components/Homepage';
import Order from 'components/Order';
import Myzone from 'components/Myzone';
import Business from 'components/Business';
import Login from 'components/Login';
import Search from 'components/Search';
import Sureorder from 'components/Sureorder'
Vue.use(Router);

export default new Router({
  mode:'history',
  base: '/front/',
  routes: [
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/myzone',
      name: 'myzone',
      component: Myzone
    },
    {
      path: '/business',
      name: 'business',
      component: Business
    },
    {
      path: '/search/:keyword',
      name: 'Search',
      component: Search
    },
       {
         path: '/',
         name: 'Login',
         component: Login
       },
    {
      path: '/homepage',
      name: 'homepage',
      component: Homepage
    },
    {
    	path:'/sureorder',
    	name:'sureorder',
    	component:Sureorder
    }
  ]
});



